@import '../assets/variables';

.about-content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.about-content__div__1 {
  background-color: $color-primary;
  // height: 60vh;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 50px;
  border-left: 5px solid $color-accent;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  color: $color-secondary;
  align-items: center;
  min-height: 90vh;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.8));
    transform: translateX(-100%);
    transition: transform 0.5s ease-in-out;
  }

  &:hover::before {
    transform: translateX(0);
  }

  p {
    // margin: 0;
    // padding: 0;
    font-size: 2vw; // Default for large screens
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    // align-items: center;
  }

  @media (max-width: 768px) {
    p {
      font-size: 4vw; // Font size for tablet screens
    }
  }

  @media (max-width: 480px) {
    p {
      font-size: 3vw; // Font size for phone screens
    }
  }
}

.about-text__1p {
  font-size: $font-size-medium;
  line-height: 1.6;
  padding-top: 20px;
  animation: fadeIn 1.5s ease-in-out;
}

.about-content__div__2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-left: 5px solid $color-accent;
  position: relative;
  overflow: hidden;
  background-color: $color-primary;
  margin-top: 30px;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform $transition-speed ease-in-out;
  width: 100%;

  &:hover {
    transform: translateY(-5px);
  }

  &__p {
    font-size: $font-size-large;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    margin: 0;
    margin: 10px;
    padding: 0;
    border-radius: 10px;
    text-align: center;
    padding: 50px;
    animation: fadeIn 1.5s ease-in-out;
    color: $color-secondary;
  }

  @media (max-width: 480px) {
    &__p {
      font-size: $font-size-medium;
    }
  }

  @media (max-width: 768px) {
    width: 100%;
    margin-top: 20px;
    padding: 20px;
  }
}

.about__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  

  @media (max-width: 768px) {
    flex-direction: column;
    width: 95%;
  }
}

.projects {
  border-radius: 10px;
  overflow: hidden;
  width: 95%;
  margin: 20px;
  transition: transform $transition-speed ease;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  // height: 700px;

  &:hover {
    // box-shadow: rgba(0, 0, 0, 0.17) inset 0px 0px 25px 0px, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    transform: scale(1.05);
  }

  &:hover .project-title-hover {
    color: $color-accent;
  }

  @media (max-width: 768px) {
    width: 100%;
  }

}

img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.05);
  }
}


.about__img {
  background-color: #6699cc;
  mix-blend-mode: multiply;
  width: 30%;
  margin-left: 20px;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 1);
  margin: 0 20px 20px 0; /* Adjust margins as needed */

  /* You can comment out or remove the following lines if not needed */
  /* height: auto; */
  /* padding-left: auto; */
  /* top: 50%; */
  /* left: 50%; */

  @media (max-width: 768px) {
    width: 80%; /* Adjusted width for phone screens */
  }
}


.about__intro {
  // border: dotted red 2px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // border: dotted red 2px;
  }
}
